<template>
    <div>
        <vx-card class="mb-base">
            <div class="md:flex my-4">
                <div class="lg:w-1/6 mt-4 md:mt-0 md:ml-6">
                <v-select data-vv-validate-on="blur" :placeholder="'-- Pilih --'" :options="pilihanA" v-model="pilihanASelected" @input="updatePilihan"></v-select>
                </div>
                <div class="lg:w-1/2 mt-4 md:mt-0 md:ml-6">
                <v-select data-vv-validate-on="blur" :placeholder="'-- Pilih --'" label="nama" :options="pilihanB" v-model="pilihanBSelected"  ></v-select>
                </div>
                <div class="lg:w-1/6 mt-4 md:mt-0 md:ml-6">
                <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal Awal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="selectedStart" ></datepicker>
                </div>
                <div class="lg:w-1/6 mt-4 md:mt-0 md:ml-6">
                <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal Akhir --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="selectedEnd" ></datepicker>
                </div>
                <div class="mt-4 md:mt-0 md:ml-6">
                <vs-button :disabled="!validateForm" radius color="primary" icon="search" v-on:click="loadDataServer"  style="display:inline; margin-right:30px;"></vs-button>
                </div>   
            </div>
        </vx-card>

        <vx-card>
            <!-- <vs-table
              :data="table"
              search>
              <template slot="thead">
                <vs-th sort-key="toko" class="bg-primary text-white text-center">
                  TOKO
                </vs-th>
                <vs-th sort-key="pos" class="bg-primary text-white">
                  JUMLAH HARI
                </vs-th>
                <vs-th class="bg-primary text-white text-center">
                  AKSI
                </vs-th>
              </template>

              <template slot-scope="{data}">
                <vs-tr :key="items.id" v-for="items in data" >
                  <vs-td>
                    {{items.toko}}
                  </vs-td>

                  <vs-td class="text-left">
                    {{items.hari}}
                  </vs-td>

                  <vs-td>
                     <vs-button type="border" color="primary" icon="search"></vs-button>
                  </vs-td>
                </vs-tr>
              </template>
            </vs-table> -->
            <table class="w-full table table-responsive">
                <thead>
                    <tr>
                        <th class="bg-primary text-white text-center p-2">TOKO</th>
                        <th class="bg-primary text-white text-center">JUMLAH HARI</th>
                        <th class="bg-primary text-white text-center">AKSI</th>
                    </tr>
                </thead>
                <tbody>
                    <tr :key="items.id" v-for="items in table" class="text-center">
                        <td class="text-center">{{items.toko}}</td>
                        <td class="text-center">{{items.hari}}</td>
                        <td><vs-button class="mx-auto my-2" type="border" color="primary" icon="search" @click="detailLaporan"></vs-button></td>
                    </tr>
                </tbody>
            </table>
        </vx-card>

        <vs-popup class="holamundo" fullscreen title="Monitoring Data Omset" :active.sync="detail">
            <table class="w-full table table-responsive">
                <thead>
                    <tr>
                        <th class="bg-primary text-white text-center p-2">TANGGAL</th>
                        <th class="bg-primary text-white text-center">COCOK</th>
                        <th class="bg-primary text-white text-center">KETERANGAN</th>
                    </tr>
                </thead>
                <tbody>
                    <tr :key="items.id" v-for="items in tableDetail" class="text-center">
                        <td class="text-center">{{items.tgl}}</td>
                        <td>
                            <div class="centerx">
                                <vs-chip class="my-2" v-if="items.cocok == 'Y'" color="success">Cocok</vs-chip>
                                <vs-chip v-else color="danger">Tidak Cocok</vs-chip>
                            </div>
                        </td>
                        <td class="text-center">{{items.keterangan}}</td>
                    </tr>
                </tbody>
            </table>
        </vs-popup>

    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'

export default {
    data() {
        return{
            detail:false,
            table:[],
            tableDetail:[],
            // Select Option 1
            pilihanA:['Region','Wilayah','Area','Cabang','Toko'],
            pilihanASelected:null,
            // Select Option 2
            pilihanB:[],
            pilihanBSelected:null,
            // Select 3
            selectedStart:null,
            // Select 4
            selectedEnd:null,
            // Selct Option 5
            selectedMonth:new Date(),
        }
    },

    components:{
        Datepicker
    },

    computed: {
        validateForm() {
        return !this.errors.any() && this.pilihanBSelected != null && this.selectedStart != null && this.selectedEnd != null;
        },
    },

    methods:{
        updatePilihan(value){

            this.$vs.loading()
            this.$http.get('toko/'+ value)
            .then(response => {
                if(response.data.status == "OK"){
                    this.pilihanB = response.data.message
                }
            })
            .catch(error => {error})
                setTimeout( ()=> {
                this.$vs.loading.close()
            }, 1000);
        },

        loadDataServer(){
            this.$vs.loading()

            var postData = {
                kode : this.pilihanBSelected.kode,
                nama : this.pilihanBSelected.nama,
                tgl_awal:  btoa(this.selectedStart),
                tgl_akhir: btoa(this.selectedEnd),
            }

            this.$http.post('/pos/toko/monitoringDataOmset', postData)
            .then(response => {
                if(response.data.status == "SUKSES") {
                    this.table = response.data.message
                }else{
                    this.$swal.fire(
                    'Gagal !',
                    response.data.message,
                    'error'
                    )
                }
            })
            .catch(error => {error})
            .finally(() =>{this.$vs.loading.close()})
            
        },

        detailLaporan(){
            this.detail = true
            this.$vs.loading()

            var postData = {
                kode : this.pilihanBSelected.kode,
                nama : this.pilihanBSelected.nama,
                tgl_awal:  btoa(this.selectedStart),
                tgl_akhir: btoa(this.selectedEnd),
            }

            this.$http.post('/pos/toko/monitoringDataOmsetDetail', postData)
            .then(response => {
                if(response.data.status == "SUKSES") {
                    this.tableDetail = response.data.message
                }else{
                    this.$swal.fire(
                    'Gagal !',
                    response.data.message,
                    'error'
                    )
                }
            })
            .catch(error => {error})
            .finally(() =>{this.$vs.loading.close()})
        }
    }
}
</script>